import React from "react";
import { Link } from "react-router-dom";
import { useDispatch, useSelector } from "react-redux";
import { getDetail } from "../actions/index";
import { useEffect } from "react";
import Card from '@mui/material/Card';
import CardContent from '@mui/material/CardContent';
import CardMedia from '@mui/material/CardMedia';
import Typography from '@mui/material/Typography';
import { CardActionArea } from '@mui/material';
/*1_ cambiar el maxwidth que quizas con eso no tengo que usar margenes.
  2_especificar que hablo de generos y plataformas
*/
export default function DetailGames({match}){
const dispatch = useDispatch()

 useEffect(() => {
     dispatch(getDetail(match.params.id));
 },[dispatch])

const myVideogame = useSelector((state) => state.details);

return (
    <div>
        {myVideogame.map((el) => { 
          return (
                <Card sx={{mx: "auto"}} key={el.id} >
                  <CardActionArea>
                    <CardMedia
                      component="img"
                      image={el.img}
                      alt="image not found"
                      sx={{maxWidth:300,maxHeight:300, mx:"auto"}}
                    />
                    <CardContent>
                      <Typography gutterBottom variant="h5" component="div">
                        {el.name}
                      </Typography>
                      {/*<Typography variant="body2" color="text.secondary">
                        {el.genres.map((el)=>{return el.name+' '})}
                      </Typography>*/}
                      <Typography variant="body2" color="text.secondary">
                        {el.genres.map((el)=>{return el+' '})}
                      </Typography>
                      <Typography variant="body2" color="text.secondary">
                        {el.platforms.map((el)=>{return el+ ', '})}
                      </Typography>
                      {/*<Typography variant="body2" color="text.secondary">
                        {el.description}
                      </Typography>*/}
                    </CardContent>
                  </CardActionArea>
                </Card>
         );
         })}
        <Link to= '/home'>
            <button>Volver</button>
        </Link>
    </div>
)
//7bdc
//39 y 4d
}

